<template>

	<view class="content">
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText"><text>返回</text></block>
			<block slot="content"><text> {{title}} </text></block>
		</cu-custom>

		<view class="center" style="padding:20upx;box-sizing: border-box;" v-if="know!=null">
			<view class="header">
				<view class="margin-tb-sm" style="font-weight: bolder;font-size: 38upx;">{{title}}</view>
				<view>
					热点数:{{know.vistorCount}}
					<view style="float: right;">{{know.creationTimeDateBeforeNow}}</view>
				</view>
				<view class="margin-tb-sm" style="height:50upx">
					<view style="float: right;color:#0081FF" @click="CreateKnowLedgeFollow()">收藏到我的词库</view>
				</view>

			</view>

			<view style=" font-weight: bolder;font-size: 38upx;text-align: center;" class="margin-tb-sm">词典详情</view>

			<u-parse className="padding-tb-sm" :content="artical" @preview="preview" @navigate="navigate"></u-parse>
		</view>
   	<view style="height:120upx"></view>


		<view class="b-fixed flex justify-between" v-if="show">
			<button class="cu-btn  round bg-red" @click="KnowLedgeFollowLevel(2)">了解</button>
			<button class="cu-btn round bg-blue" @click="KnowLedgeFollowLevel(3)">熟悉</button>
			<button class="cu-btn round bg-gradual-green" @click="KnowLedgeFollowLevel(4)">掌握</button>
		</view>


	</view>
</template>

</view>
</template>

<script>
	export default {
		data() {
			return {
				title: "",
				id: "",
				know: null,
				show:true,
			}
		},
		onLoad(options) {
			console.log(options);
			this.id = options.id;
		},
		onShow(options) {
			this.GetKnowLedge();
		},
		methods: {
			async GetKnowLedge() {
				let url = "/WxApi/KnowLedgeWx/GetKnowLedge";

				this.$comm.ShowLoading();
				let {
					success,
					data,
					code
				} = await this.$http.PostAsync(url, {
					knowLedgeId: this.id
				});
				console.log("aa", code);
				if (success == true) {
					this.know = data;
					this.title = data.name;
					this.artical = data.description;
				} else if (code == "400") {
					console.log('执行');
					this.$comm.CheckToken();
				}
				uni.hideLoading()
			},
			async CreateKnowLedgeFollow() {
				this.$comm.ShowLoading();
				let {
					success,
					data
				} = await this.$http.PostAsync("/WxApi/KnowLedgeWx/CreateKnowLedgeFollow", [{
					knowLedgeId: this.id
				}]);

				uni.hideLoading()
			},
			async KnowLedgeFollowLevel(level)
			{
				this.$comm.ShowLoading();
				let {
					success,
					data
				} = await this.$http.PostAsync("/WxApi/KnowLedgeWx/KnowLedgeFollowLevel", 
					{knowLedgeId: this.id,
					  masterLevel:level
					}
				);
				
				uni.hideLoading()
				if(success){
				this.show=false;
				}
		    },
				
			preview(src, e) {
				// do something
			},
			navigate(href, e) {
				// do something
			}
		}
	}
</script>

<style>
	page {
		background-color: white;

	}

	.b-fixed {
		position: fixed;
		bottom: 0upx;
		left: 0upx;
		width: 100%;
		height: 100upx;
        padding:20upx;
		background-color: white;
	}
</style>
